.#{$prefix}table-container {
  padding: 10px;
  background: $gray3;
  border: 1px solid $gray2;
  margin-top: 20px;
   summary {
    font-weight: bold;
    cursor: pointer;
  }
}


.#{$prefix}table {
  height: 300px;
  overflow-y: scroll;
  border: 1px solid $gray2;
  margin-top: 10px;

  table {
    font-size: 12px;
    border-collapse: collapse;
    width: 100%;
    font-family: $mono;
  }
  thead {
    background: $gray3;
    text-align: left;
    border-bottom: 1px solid $gray2;
  }
  .sortable {
    background-image: url("/img/sort.png");
    background-repeat: no-repeat;
    background-position: 100% 4px;
    background-size: 12px;
    cursor: pointer;
  }

  .sort--ascending {
    background-image: url("/img/sort-up.png");
  }

  .sort--descending {
    background-image: url("/img/sort-down.png");
  }

  table tr:nth-child(odd) {
    background: #fff;
  }

  td,
  th {
    padding: 4px;
  }
}
